@import url(https://fonts.googleapis.com/css2?family=Crafty+Girls&display=swap);
body {
  padding: 50px;
  margin: 0;
  font-family: 'Crafty Girls', cursive;
  background-color: #97b3ff;
}

#root {
  display: flex;
  justify-content: center;
}


.users-main form input {
  padding: 10px;
  font-size: 18px;
  background-color: transparent;
  border: 1px solid #0066ff;
  color: white;
  width: 170px;
  outline: 0;
  font-family: "Crafty Girls", cursive;
}

h1 {
  text-align: center;
  color: #0066ff;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

::-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: white;
}

.users-main form button {
  padding: 10px 15px;
  font-size: 18px;
  margin: 10px;
  background-color: transparent;
  border: 1px solid #0066ff;
  color: #0066ff;
  outline: 0;
  cursor: pointer;

  font-family: "Crafty Girls", cursive;
}

.users-main form button:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px #0066ff;
  transition: 0.5s;
}

.list {
  list-style: none;
  padding-left: 0;
  width: 250px;
}

.list li {
  color: white;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 10px;
  transition: 0.5s;
  position: relative;
}

.list li:hover {
  background-color: pink;
}

.list li span {
  position: absolute;
  right: 10px;
  font-weight: bold;
  transition: 0.5s;
}

.list li span:hover {
  cursor: pointer;
  color: red;
}

